<template>
  <div class="design page-container">
    <div v-for="(v, i) in DESIGN_CONTENTS" :key="i" class="info-paragraph">
      <h2 class="fs-36 mb-36" v-html="v.title"></h2>
      <template v-for="(item, index) in v.paragraphs">
        <img
          :key="index + 'i'"
          v-if="item.type && item.type == 'img'"
          class="info-img"
          :class="item.class"
          :src="getImg(item.url)"
        />
        <div
          :key="index + 'r'"
          :class="item.class"
          v-else-if="item.type && item.type == 'rich'"
          v-html="item.content"
        ></div>
        <div
          :key="index + 's'"
          class="slide-box"
          :class="item.class"
          @mouseleave="resetSlide(item.name)"
          v-else-if="item.type && item.type == 'slide'"
        >
          <template v-for="(temp, idx) in item.images">
            <img
              :key="idx"
              class="slide-img"
              :class="getClass(idx, item.name)"
              :src="getImg(temp)"
              @mouseenter="onHover(idx, item.name)"
            />
          </template>
        </div>
        <div
          :key="index + 'sw'"
          class="position-relative w-100 overflow-hidden"
          :class="item.class"
          v-else-if="item.type && item.type == 'swipe'"
        >
          <img
            class="img-icon arrow-left"
            @click="onSwipe(0)"
            :src="getImg('aftersale/arrowleft.png')"
          />
          <img
            class="img-icon arrow-right"
            @click="onSwipe(1)"
            :src="getImg('aftersale/arrowright.png')"
          />
          <template v-for="(temp, idx) in item.images">
            <img
              :key="idx"
              class="w-100 swipe-img"
              :class="idx == 0 && swipeIndex ? 'swiped' : ''"
              :src="getImg(temp)"
            />
          </template>
        </div>
        <img-viewer
          :key="index + 'iv'"
          :class="item.className"
          :galleryID="item.id"
          :images="item.images"
          v-else-if="item.type && item.type == 'imgViewer'"
        />
        <p class="fs-20 fw-3" :key="index + 'p'" v-else v-html="item"></p>
      </template>
    </div>
  </div>
</template>
      
<script>
import { DESIGN_CONTENTS, SLIDE_INDICES } from "@/constant";
import imgViewer from "@/components/img-viewer.vue";
export default {
  name: "DesignPage",
  components: { imgViewer },
  data() {
    return {
      DESIGN_CONTENTS,
      slideIndices: {},
      swipeIndex: 0,
    };
  },
  computed: {
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
    getClass() {
      return function (index, name) {
        const chosen = this.slideIndices[name];
        if (index == chosen) {
          return "active";
        } else if (index == chosen + 1 || index == chosen - 1) {
          return "secondary";
        }
        return "";
      };
    },
  },
  methods: {
    onHover(index, name) {
      this.slideIndices = { ...this.slideIndices, [name]: index };
    },
    resetSlide(name) {
      this.slideIndices = { ...this.slideIndices, [name]: SLIDE_INDICES[name] };
    },
    onSwipe(val) {
      this.swipeIndex = val;
    },
  },
  mounted() {
    this.slideIndices = { ...SLIDE_INDICES };
  },
};
</script>
      
<style>
@import "../style/design.scss";
</style>

      
      